@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhai+2:wght@600&family=Fraunces:opsz,wght@9..144,700&family=Fredoka+One&family=Merriweather&family=Montserrat:wght@100;400;500;700&family=Nunito&family=Rubik+Spray+Paint&family=Sacramento&family=Ubuntu:wght@300&display=swap);
.header{
    width:100%;
    display:flex;
    align-items: center;
    margin-bottom: 12px;
    margin-top: 0px;
}

.header-logo{
    height:27px;
    max-width: 140px;
    margin-right: 24px;
}

.header-right{
    display: flex;
    flex: 1 1;
    justify-content: space-between;
    align-items: center;
    height: 72px;


}

.header-location-search-container{
    height:54px;
    display: flex;
    align-items: center;
    border-radius: 8px;
    box-shadow: var(--primary-box-shadow);
    border: 1px solid var(--border-white);
    width: 70%;
}

.location-wrapper{
    display: flex;
    flex:1 1;
    justify-content: space-between;
    padding: 0px 10px;
}

.location-icon-name{
    display: flex;
    color: var(--zomato-gray);
    justify-content: flex-start;
    
}

.location-icon{
    color:var(--zomato-red);
    font-size: 20px;
    margin-right: 8px;
    width:16.4;
    height:20px;
    color: #cab1e8;;
}

.location-search-separartor{
    height:20px;
    border:0.5px solid var(--gray);
}

.header-searchbar{
    margin-right:8px;
    flex:2 1;
}

.search-icon{
   height :19px;
    color:var(--zomato-gray);
    margin:0px 12px;
    position: relative;
    top:4px;

}
.city-name{
    margin-bottom: 0;
}

.caret-icon{
    width:16px;
    height:16px;
    color: var(--zomato-gray);
}
.search-input{
    border: none;
    outline:none;
    width:60%;
    font-size: 14px;
}

.profile-wrapper{
    display: flex;
    justify-content: space-between;
    width: 15%;
    align-items: center;
}

.header-profile-image{
    height:50px;
    width:50px;
   
}
.tab-options{
 box-shadow: rgb(232, 232, 232) 0px -0.5px 0px inset;
}

.options-wrapper{
    display: flex;
}

.tab-item{
    margin-right: 24px;
    padding: 12px 12px 20px 12px;
}

.tab-name{
    font-size: 20px;
    font-weight: 600;
    margin-left: 10px;
    color:var(--inactive-text);
}

.tab-image-container{
    height: 60px;
    width: 60px;
    border-radius: 50%;
    background-color: var( --inactive-color);
}

.tab-image{
    height: 30px;
    width:30px;
}

.active-tab{
    border-bottom: 2px solid var(--zomato-red);
}

.active-tab .tab-name{
    color: var(--zomato-red);
}
.filters{
    padding: 16px 0px;
    display: flex;
    flex-wrap: wrap;
}

.filter-item{
    display: flex;
    background-color:var(--primary-bg) ;
    padding: 8px;
    color:var(--filter-text);
  border: 1px solid var(--gray);
  border-radius: 8px;
  margin:10px 10px 10px 0;
  font-size: 14px;
}

.filter-icon{
    color: var(--gray);
}
.filter-icon{
    width: 18px;
    height: 18px;
    -webkit-filter: brightness(0.5);
            filter: brightness(0.5);
    margin-right:5px;
    
}


.delivery-collection{
    background-color: var(--collection-bg);
   padding: 40px 0px;
}


.delivery-item-cover{
    width:150px;
    height:150px;
    
  
 
    box-shadow: var(--collection-shadow);
    border-radius: 50%;
    display: flex;
    align-items: center;
}


.delivery-item-image{
   object-fit: cover;
   height:100%;
   width:100%;
   border-radius:50%;
   
   
}
.delivery-item-title{
    font-size: 20px;
    font-weight: 500;
    text-overflow: ellipsis;
    overflow: hidden;
    margin: 10px 50px;
    
 
}

.delivery-container{
   
}
.delivery-brand-collection{
    padding: 40px 0px;
}
.explore-section{
    margin: top 50px ;
}

.explore-grid{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}


.explore-card-image {
    width: 100%;
    height: 100%;
    border-radius: 15px;
    object-fit: cover;
}

.delivery-time {
    position: absolute;
    right: 10px;
    bottom: 10px;
    opacity: 0.8;
    background-color: var(--primary-bg);
    -webkit-backdrop-filter: blur(4px);
            backdrop-filter: blur(4px);
    font-weight: 600;
    font-size: 12px;
    padding: 2px 5px 3px;
    border-radius: 4px;
}
.explore-bottom{
    display: flex;
    align-items: center;
    max-width: 100%;

}

.res-bottom-text{
    font-size: 12px;
    color:var(--inactive-text);
    max-width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-left: 8px;
}

.explore-card {
    margin: 12px 0px 24px 0px;
    padding: 10px;
    border-radius: 15px;
    border: 1px solid transparent;
}

.explore-card-cover {
    width: 328px;
    height: 248px;
    border-radius: 15px;
    position: relative;
}

.pro-off {
    position: absolute;
    left: 0px;
    bottom: 40px;
    background-color: var(--tag-red);
    color: var(--primary-bg);
    font-size: 14px;
    padding: 0px 6px;
    border-radius: 0px 2px 2px 0px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    line-height: 18px;
    font-weight: 500;
}
.star{
    width: 8.02px;
    height:8.02px;
    -webkit-filter: invert(1);
            filter: invert(1);
    
}

.discount {
    position: absolute;
    left: 0px;
    bottom: 14px;
    background-color: var(--tag-blue);
    color: var(--primary-bg);
    font-size: 14px;
    padding: 0px 6px;
    border-radius: 0px 2px 2px 0px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    line-height: 18px;
    font-weight: 500;

}
.gold-off {
    position: absolute;
    left: 0px;
    bottom: 14px;
    background-color: var(--tag-red);
    color: var(--primary-bg);
    font-size: 14px;
    padding: 0px 6px;
    border-radius: 0px 2px 2px 0px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    line-height: 18px;
    font-weight: 500;

}

.res-row{
    display: flex;
    justify-content: space-between;
    padding: 8px 0px 6px ;

}

.res-name{
    max-width:200px ;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 18px;
    font-weight: 600;
    color:var(--heading-text);
}
.res-rating{
    padding:0px 8px;
    font-size: 13px;
    font-weight:600 ;
    color: var(--primary-bg);
    background-color: var(--tag-green);
    border-radius: 6px;
    height:24px;

}
.res-rating img{
    font-size: 10px;
    margin-left: 2px;
}
.res-cuisine{
    max-width: 56%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 14px;
}

.res-cuisine-tag{
    margin-right:4px;
    color:var(--inactive-text);

}

.res-price{
    font-size: 14px;
    color:var(--inactive-text);

}

.card-seprator{
    height:1px;
    margin:10px 0px 5px;
    background-color:var(--border-white);

}

.explore-card:hover{
    background-color: var(--primary-bg);
    border: 1px solid var(--border-white);
    box-shadow: 1px 1px 0px rgba(210, 207, 207), 1px 1px 0px rgba(217, 212, 212);
}
.explore-bottom-text{
    font-size: 12px;
    margin-left: 9px;
}
.collection-wrapper{
    background-color: var(--inactive-color);
   

}
.collection{
    padding: 15px 0px 40px;

}
.collection-subtitle-row{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}
.collection-subtitle-text{
    color:var(--subtitle-text);
    font-size: 18px;
}
.collection-location{
    color: var(--zomato-red);
    display: flex;
}


.coll{
    display: flex;
}
.collection-cover{
    height: 320px;
    width: 260px;
    margin-right: 20px;
    border-radius: 6px;
    position: relative;
}
.gradient-bg{
    position: absolute;
    height:100%;
    width: 100%;
    background: var(--bg-gradient);
    top:0px;
    left:0px;
    border-radius: 6px;

}

.collection-image{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.collection-card-title{
    position: absolute;
    left: 16px;
    bottom: 30px;
    font-size: 18px;
    color:  var(--primary-bg);

}

.collection-card-subtitle{
    display: flex;
    color: var(--primary-bg);
    position: absolute;
    left: 16px;
    bottom: 10px;
    font-size: 14px;

}



.max-width{
    max-width:1110px;
    margin:0 auto;
}

.absolute-center{
    display: flex;
    align-items: center;
    justify-content: center;
    
}

.cur-po{
cursor: pointer;
}

.collection-title{
    font-size: 30px;
    color:var(--heading-text);
    font-weight: 600;
    margin-bottom: 32px;
}
#root{
    --primary-box-shadow:rgb(28 28 28 / 8%) 0px 2px 8px;
    --border-white:rgb(232,232,232);
    --zomato-gray:#828282;
    --zomato-red:#ff7e8b;
    --gray:rgb(207,207,207);
    --inactive-text:#696969;
    --inactive-color:#F8F8F8;
    --filter-text:#9c9c9c;
    --heading-text:#1C1C1C;
    --collection-shadow:rgb(0 0 0/ 8%) 0px 3px 12px;
    --primary-bg:rgb(255,255,255);
    --collection-bg:#F8F8F8;
    --tag-red:#d92662;
    --tag-blue:#256fef;
    --tag-green:#267e3e;
    --subtitle-text:#363636;
    --bg-gradient:linear-gradient(0deg, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0.05) 50%, rgba(0, 0, 0, 0.05) 85%);

} 
.body{
  font-family: 'Nunito', sans-serif;
  margin:0px;
  box-sizing: border-box;
  font-family: Okra, Helvetica, sans-serif;
}



